<template>
    <div class="row container-gat box-margin-top event-detail-container">
        <div class="event-detail-left inline-block">
            <app-gat></app-gat>
        </div>
        <div class="event-detail-right inline-block">
            <div>
                <a href="\" class="text-color-category medium-text-form-m">{{label.home_page}}</a>
                <span class="text-color-category medium-text-form-m event-detail-margin-right-left">
                    <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                </span>
                <span class="text-color-category medium-text-form-m">{{label.event}}</span>
                <span class="text-color-category medium-text-form-m event-detail-margin-right-left">
                    <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                </span>
                <span class="text-color-highlight medium-text-form-m">{{eventDetail.name}}</span>
            </div>
            <div class="event-detail-right-container">
                <p class="super-super-biger-text-form-m text-color-default text-limit event-detail-title">{{eventDetail.name}}</p>
                <div class="event-detail-info">
                    <div class="left-side medium-text-form-r text-color-author">
                        <span>
                            <i class="fa fa-clock-o fa-lg" aria-hidden="true"></i>
                        </span>
                        <span>{{eventDetail.time}}</span>
                    </div>
                    <div class="right-side medium-text-form-r text-color-highlight">
                        {{eventDetail.address}}
                    </div>
                </div>
                <div class="box-block event-detail-img event-detail-margin-top">
                    <img class="image-fill img-book" src="../../../assets/images/event_2.png" :alt="eventDetail.name">
                </div>
                <vue-perfect-scrollbar class="medium-text-form-r text-color-default event-detail-content event-detail-margin-top">{{eventDetail.descript}}</vue-perfect-scrollbar>
            </div>
        </div>
    </div>
</template>

<script>
import CommonService from '../../../services/CommonService.js';
import Label from '../../../common/Label.js';
import VuePerfectScrollbar from 'vue-perfect-scrollbar';
import AppGat from '../../common/appGat/AppGat.vue';

export default {
    name: 'event-detail',
    mixins: [CommonService, Label],
    components: { VuePerfectScrollbar, AppGat},
    data() {

        return {
            eventDetail: {
                id: 1,
                address: "Cafein Bookstop",
                time: "18 tháng 08 năm 2017",
                name: "Sự kiện lớn và thời gian gần nhất",
                imageUrl: "",
                descript: "Sau đó gần một năm, cậu quyết định rời tiệm pha lê vì cậu đã có đủ tiền mua một bầy cừu mới, gấp đôi số cừu cậu bán đi hồi trước. Nhưng cậu lại không mua một con cừu nào cả. Bởi vì cậu quyết định đi tìm Truyền thuyết bản thân của mình -Hòn Đá Triết Gia trong giấc mơ kia. Cậu gia nhập đoàn lữ hành đến một sa mạc nơi gần các kim tự tháp. Trong đoàn người, cậu gặp một người Anh đã tìm kiếm những nhà giả kim chân chính suốt 20 năm nay. Người Anh này có hàng thùng sách về thuyết giả kim khiến cậu kinh ngạc. Cậu học được rất nhiều điều dọc đường đi. Đoàn người tiến dần về phía ốc đảo. Khi người Anh nọ thử quan sát sa mạc và học ngôn ngữ sa mạc, thì Edward đọc những cuốn sách của anh ta và học về thuật giả kim. Người Anh nói với cậu rằng mục đích của những nhà giả kim là tinh lọc kim loại bằng cách nung luyện trong nhiều năm. Khi họ tới ốc đảo, đoàn lữ hành được chào đón, và được khuyên phải dừng bước vì có chiến tranh giữa các bộ tộc phía trước. Cậu đã giúp người Anh tìm một nhà giả kim. Và ở nơi đó, cậu gặp Fatima, và yêu cô ngay từ cái nhìn đầu tiên. Fatima là một người phụ nữ sa mạc và cô cũng đã chờ đợi người đàn ông của đời mình từ rất lâu rồi cho tới khi gặp chàng. Trong một lần đánh cược cuộc đời của mình vào trực giác và sự nhạy bén của mình, cậu đã giúp bộ tộc tránh được một cuộc tấn công đột ngột vào ốc đảo. Cậu được thưởng rất nhiều tiền vàng, cùng một lời đề nghị trở thành người cố vấn quan trọng cho trưởng tộc.Cũng chính lúc ấy nhà giả kim ở ốc đảo nhận ra rằng ông đã tìm thấy một học trò xuất sắc. Hiển nhiên đó chính là Santiago. Cậu lưỡng lự khi phải rời ốc đảo vì tình yêu với Fatima cũng như cuộc sống khá giàu sang và danh vọng. Nhưng nhà giả kim đã báo trước cậu sẽ mất đi khả năng nhìn thấy điềm báo, rồi sẽ mất chức và sẽ hối hận vì đã không theo đuổi giấc mơ của mình để đi tìm kho tàng. Sau đó, với sự khích lệ của Fatima, cậu đã rời ốc đảo, lên đường đi tới Kim Tự Tháp. Tới một vùng sa mạc cai quản bởi những chiến binh hung dữ, cậu phải vượt qua những cửa ải, trổ tài biến thành gió, thấu hiểu ngôn ngữ của vũ trụ thì mới được phép đi qua. Tới đây, nhà giả kim từ biệt chàng trai và chàng một mình đi tới chân kim tự tháp. Cậu đã đến được Kim Tự Tháp, rồi lại một lần trắng tay khi đụng độ những tên cướp. Nhưng cậu đã tìm ra vị trí đặt kho báu, ở một nơi rất bất ngờ. Cuối cùng Santiago đã lấy được kho báu, trở lại ốc đảo với niềm vui hân hoan và niềm hạnh phúc tuyệt vời.",
            }
        }
    },
    mounted(){
        
    },

    computed: {
    },

    methods: {
    },
}
</script>


<style lang="scss">
@import '../../../scss/text_styles.scss';
@import '../../../scss/common_styles.scss';
@import 'EventDetail.scss';
</style>



